const vscode = require('vscode');
const util = require('../util');
const fs = require('fs');
const path = require('path');
/**
 *  message.cmd 
 */
const messageHandler = {
    /**
     * 
     * @param {*} panel 
     * @param {*} message 
     * @param {*} resp 
     */
    data: null,
    invokeCallback(panel, message, resp) {
        console.log('', resp);
        // 400-600
        if (typeof resp == 'object' && resp.code && resp.code >= 400 && resp.code < 600) {
            util.showError(resp.message || '');
        }
        panel.webview.postMessage({cmd: 'vscodeCallback', cbid: message.cbid, data: resp});
    },
    alert(global, message) {
        util.showInfo(message.msg);
    },
    dataChange(global, message) {
        if (message['data']) {
            this.data = message['data']
        }
    },
    getData(global, message) {
        this.invokeCallback(global.panel, message, this.data);
    },
};
module.exports = function(context) {
    function createPanel() {
        let panel = vscode.window.createWebviewPanel(
            'showJson', // viewType
            "JSON", // 
            vscode.ViewColumn.One, // 
            {
                enableScripts: true, // JS
                retainContextWhenHidden: true,// webview
            }
        );
        let global = { panel};
        
        panel.webview.html = util.Web.getWebViewContent(context, 'lib/view/showJson/showJson.html');
        panel.webview.onDidReceiveMessage(message => {
            if (messageHandler[message.cmd]) {
                messageHandler[message.cmd](global, message);
            } else {
                util.showError(` ${message.cmd} !`);
            }
        }, undefined, context.subscriptions);
        return panel
    }
    let panel
    context.subscriptions.push(vscode.commands.registerCommand('extension.demo.showJson', function () {
        if (!panel){//
            panel = createPanel()
            panel.onDidDispose(() =>{
                panel = null
            })
        }
        let _arguments = arguments;
        if (_arguments.length > 0 && _arguments[0]['data'] && _arguments[0]['title'])//
            {
                _arguments[0]['data']['__time'] = new Date();
                messageHandler.data = messageHandler.data || {};
                messageHandler.data[_arguments[0]['title']] = _arguments[0]['data'] || {};
                panel.webview.postMessage([{'action': 'refresh'}]);
            }
    }));
    
    //  
    context.subscriptions.push(vscode.commands.registerTextEditorCommand('extension.weblog', (textEditor, edit) => {
        vscode.window.createWebviewPanel(
            'weblog', // viewType
            "weblog", // 
            vscode.ViewColumn.One, // 
            {
                enableScripts: true, // JS
            }
        );
        panel.webview.html = util.Web.getWebViewContent(context, 'lib/view/showJson/showJson.html');
        panel.webview.onDidReceiveMessage(message => {
            if (messageHandler[message.cmd]) {
                messageHandler[message.cmd](global, message);
            } else {
                util.showError(` ${message.cmd} !`);
            }
        }, undefined, context.subscriptions);
    }));
};
