const vscode = require('vscode');
const path = require('path');
const fs = require('fs');
const util = require('./util');

module.exports = function(context) {
    let b = {
        /**
         * package.jsondependenciesdevDependencies
         * 
         * @param {*} document 
         * @param {*} position 
         * @param {*} token 
         */
        provideHover(document, position, token) {
            return new Promise(resolve => {
                const fileName    = document.fileName;
                const workDir     = path.dirname(fileName);
                const word        = document.getText(document.getWordRangeAtPosition(position));

                let url = '/api/'
                // 
                let ControllerName = util.Document.isController(document);
                if (ControllerName){
                    url += ControllerName + '/';
                } else return;
                let _text = document.lineAt(position.line).text
                let _match = _text.match(/^\s+public.+\s+?(.+?)\((.+?)?\)/)
                if (!_match) {
                    return;
                }
                else{
                    if (word != _match[1]) return;
                    url += _match[1]
                }
                setTimeout(() => {
                    resolve(new vscode.Hover(`* ${url}\n* **2**${word}\n* ****${1}`));
                }, 100);//
            });
        }
    };
    //  C#
    context.subscriptions.push(vscode.languages.registerHoverProvider(
        [{ 
            scheme: 'file', language: 'csharp',
            //  
            pattern: '**/ApiControllers/**'
        }]
        , b));
};